/* Menu principal */
.navbar-menu {
    display: flex;
    justify-content: center; /* Centre les liens horizontalement */
    flex-wrap: wrap; /* Permet aux éléments de passer à la ligne si nécessaire */
    transition: max-height 0.3s ease-out;
    overflow: hidden;
    gap: 10px; /* Ajoute de l'espace entre les éléments */
}

/* Liens du menu */
nav ul {
    list-style: none;
    display: flex;
    justify-content: center; /* Centre les liens dans la liste */
    flex-wrap: wrap; /* Permet aux liens de passer à une nouvelle ligne */
    padding: 0;
    margin: 0;
    gap: 10px; /* Ajoute de l'espace entre les liens */
}

/* Menu caché par défaut sur mobile */
@media (max-width: 768px) {
    .navbar-menu {
        flex-direction: column; /* Colonne pour les petits écrans */
        justify-content: space-evenly; /* Espace égal entre les éléments */
        align-items: center; /* Centre les éléments dans la colonne */
        gap: 15px; /* Ajoute de l'espace entre les éléments */
    }

    nav ul {
        flex-direction: column; /* Aligne les liens verticalement */
        justify-content: center; /* Centre les liens */
        align-items: center; /* Centre les éléments dans la colonne */
    }

    nav ul li a {
        width: auto; /* Ajuste la largeur automatiquement */
        text-align: center; /* Centre le texte */
        margin: 5px 0; /* Ajoute un espacement entre les liens */
    }
}
