/* Conteneur global */
.portfolio-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
    height: 600px;
    overflow-y: auto; 
}


/* Conteneur des éléments portfolio (alignement horizontal) */
.portfolio-items {
    display: flex;
    flex-wrap: wrap; /* Permet aux éléments de se réorganiser sur les petits écrans */
    gap: 20px; /* Espacement entre les éléments */
    justify-content: center; /* Centrer les éléments horizontalement */
}

/* Section d'une étude de cas */
.portfolio-item {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    background: linear-gradient(to right, #003f5c, #6a9cb8);
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
    margin-bottom: 40px;
}

/* Contenu texte + image en ligne */
.portfolio-content {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 20px;
}

/* Texte à gauche */
.portfolio-text {
    flex: 1;
    color: white;
}

/* Image à droite */
.portfolio-image {
    flex: 1;
    max-width: 400px;
    border-radius: 8px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s;
}

.portfolio-image:hover {
    transform: scale(1.02);
}

/* Bouton CTA au centre */
.portfolio-cta-container {
    margin-top: 20px;
    text-align: center;
}

.portfolio-cta {
    display: inline-block;
    padding: 12px 25px;
    background-color: #007bff;
    color: white;
    font-size: 1.2em;
    text-decoration: none;
    border-radius: 5px;
    transition: background-color 0.3s;
}

.portfolio-cta:hover {
    background-color: #0056b3;
}

/* Responsive design */
@media (max-width: 800px) {
    .portfolio-content {
        flex-direction: column;
    }

    .portfolio-image {
        max-width: 100%;
    }
}
